import axios from "axios"
import 'babel-polyfill';
import dompurify from "dompurify"

const ACTIVE_CLASS = 'search__result--active';

export default function typeAhead(search) {
  if (!search) return;
  const searchInput = search.querySelector('input[name="search"');
  const searchResults = search.querySelector('.search__results');

  searchInput.on('input', async function () {
    if (!this.value) {
      searchResults.style.display = 'none';
      return;
    }
    // Show Search Results
    searchResults.style.display = 'block';
    searchResults.innerHTML = '';
    // Get The Results
    try {
      const res = await axios.get(`/api/v1/search?q=${this.value}`);
      if (res.data.length) {
        searchResults.innerHTML = dompurify.sanitize(searchResultsHTML(res.data));
        return;
      }
      // Tell them nothing came back
      searchResults.innerHTML = `
        <div class="search__result">
          No results for ${dompurify.sanitize(this.value)} found!
        </div>
      `
    } catch (error) {
      throw new Error(error);
    }
  });

  searchInput.on('keyup', (e) => {
    if (!['Enter', 'ArrowUp', 'ArrowDown'].includes(e.key)) return;
    const current = search.querySelector(`.${ACTIVE_CLASS}`);
    const items = search.querySelectorAll('.search__result');
    let next;

    if (e.key === 'ArrowDown' && current) {
      next = current.nextElementSibling || items[0];
    } else if (e.key === 'ArrowDown') {
      next = items[0];
    } else if (e.key === 'ArrowUp' && current) {
      next = current.previousElementSibling || items[items.length - 1];
    } else if (e.key === 'ArrowUp') {
      next = items[items.length - 1];
    } else if (e.key === 'Enter' && current.href) {
      window.location = current.href;
      return;
    }
    if (current) {
      current.classList.remove(ACTIVE_CLASS);
    }
    next.classList.add(ACTIVE_CLASS)

  })

}

function searchResultsHTML(stores) {
  return stores.map(store => {
    return `
    <a href="/store/${store.slug}" class="search__result">
      <strong>
        ${store.name}
      </strong>
    </a>
    `;
  }).join('')
}

